.paginationBox {
    display: flex;
    justify-content: center;
    margin: 6vmax;
}
    
.pagination {
    display: flex;
    justify-content: center;
    padding: 0;
}

.page-item {
    background-color: rgba(226, 221, 221, 0.49);
    list-style: none;
    border: 1px solid black;
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
}

.page-item:first-child {
    border-radius: 5px 0 0 5px;
}

.page-item:last-child {
    border-radius: 0 5px 5px 0;
}

.page-link {
    text-decoration: none;
    font: 500 0.8vmax "Roboto";
    color: rgb(112, 78, 78);
    transition: all 0.3s;

}

.page-item:hover {
    background-color: white;
}

.page-item:hover .page-link {
    color: black;
}

.pageItemActive {
    background-color: tomato;
}

.pageLinkActive {
    color: white
}