.loader {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .loader-circle {
    width: 12px;
    height: 12px;
    background-color: #3498db;
    border-radius: 50%;
    animation: loader-bounce 1.4s ease-in-out infinite;
  }
  
  .loader-circle-1 {
    animation-delay: -0.32s;
  }
  
  .loader-circle-2 {
    animation-delay: -0.16s;
  }
  
  .loader-circle-3 {
    animation-delay: 0s;
  }
  
  @keyframes loader-bounce {
    0%, 80%, 100% {
      transform: scale(0);
    }
  
    40% {
      transform: scale(1);
    }
  }
  